div.form {
  display: grid;
  grid-template-columns: max-content max-content;
  grid-gap: 5px;
}
div.form label {
  text-align: left;
}
/* div.form label:after { content: ":"; } */

.container {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-auto-rows: 1fr;
  grid-column-gap: 30px;
}
.container-view-team-members {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-auto-rows: 1fr;
  grid-gap: 25px;
}
