@import url("https://fonts.googleapis.com/css2?family=Barlow");

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: Barlow;
}
#root {
  height: 100vh !important;
}
.dispFlex {
  display: flex;
}

.field-icons {
  width: 25px;
  height: 25px;
}

.forgot-password {
  color: #159eda;
  font-size: small;
}

.forgot-password:visited {
  color: #159eda;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  /* display: none; <- Crashes Chrome on hover */
  -webkit-appearance: none;
  margin: 0; /* <-- Apparently some margin are still there even though it's hidden */
}

input[type="number"] {
  -moz-appearance: textfield; /* Firefox */
}

.center-cropped {
  object-fit: none;
  object-position: center;
  height: 100px;
  width: 100px;
}

.test {
  border: #da5315 solid 1px;
}
/* .dialog {
  opacity: 0.95;
  background-color: #000000;
} */
.MuiBadge-root .MuiBadge-badge {
  background-color: inherit !important;
  /* padding: 0px; */
}

.MuiDialogTitle-root,
.MuiDialogContentText-root,
.MuiTypography-h6,
.MuiTypography-body1,
.MuiTypography-caption {
  font-family: "Barlow" !important;
}

.MuiDrawer-paper {
  z-index: 0 !important;
}

.container_layout {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(12, 1fr);
  grid-template-rows: 50px auto;
  grid-template-areas:
    "m m m m h h h h h h h h"
    "m m m m c c c c c c c c";
}

.header {
  grid-area: h;
}
.menu {
  grid-area: m;
}
.content {
  grid-area: c;
}

.MuiRating-icon {
  color: #159eda !important;
}

.ratingContainer {
  height: 100%;
  display: grid;
  grid-template-columns: 1fr;
  grid-auto-rows: 1fr;
  grid-row-gap: 10px;
}

.ratingBtn {
  grid-column: 1/-1;
  justify-self: center;
  margin-top: 40px;
}
