@import url(https://fonts.googleapis.com/css2?family=Barlow);
.profile {
  display: flex;
}

.card {
  padding: 10px 20px;
  /* width: 100%;
  height: 300px; */
  margin-bottom: 40px;
}

.profile-img {
  width: 100%;
  margin: 0 auto;
  text-align: center;
}

.profile-img-items {
  /* height: 300px; */
  width: 100%;
  margin: auto;
}

.scrollSection {
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  scrollbar-width: none; /* Firefox */
  overflow-y: auto;
}
.scrollSection::-webkit-scrollbar {
  display: none; /* Safari and Chrome */
}

.container {
  width: 95%;
  margin: auto;
}

.dropdown {
  display: flex;
  width: 95%;
  justify-content: right;
}

div.form {
  display: grid;
  grid-template-columns: -webkit-max-content -webkit-max-content;
  grid-template-columns: max-content max-content;
  grid-gap: 5px;
}
div.form label {
  text-align: left;
}
/* div.form label:after { content: ":"; } */

.container {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-auto-rows: 1fr;
  grid-column-gap: 30px;
}
.container-view-team-members {
  height: 100%;
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  grid-auto-rows: 1fr;
  grid-gap: 25px;
}

