.profile {
  display: flex;
}

.card {
  padding: 10px 20px;
  /* width: 100%;
  height: 300px; */
  margin-bottom: 40px;
}

.profile-img {
  width: 100%;
  margin: 0 auto;
  text-align: center;
}

.profile-img-items {
  /* height: 300px; */
  width: 100%;
  margin: auto;
}

.scrollSection {
  -ms-overflow-style: none; /* Internet Explorer 10+ */
  scrollbar-width: none; /* Firefox */
  overflow-y: auto;
}
.scrollSection::-webkit-scrollbar {
  display: none; /* Safari and Chrome */
}
