.link {
  text-decoration: none;
  color: #159eda;
  border-radius: 2px;
  cursor: pointer;
}

.link img {
  display: block;
  width: 40px;
}

.link label {
  align-items: center;
  justify-content: center;
  display: flex;
  width: 100%;
  margin-left: 15px;
}

.drawer {
  width: 260px;
  flex-shrink: 0;
}

.drawer .MuiDrawer-paper {
  width: 260px;
  box-sizing: border-box;
}
